import { Code } from "@nextui-org/react";
import { useStore } from "../../stores/store";

export default function TestPage() {
  const worksheets = useStore.getState().worksheets;
  const worksheet = worksheets[0];

  let pages = [];

  for (let i = 0; i < worksheet.pages.length; i++) {
    if (worksheet.pages[i].isSelected) {
      let base64str = worksheet.pages[i].previewURL.split(",")[1];
      // pages.push(base64str.slice(0, 3) + "..." + base64str.slice(-10));
      pages.push(base64str);
    }
  }

  let answerKey = worksheet.answerKey;

  let request = { pages: pages, answerKey: answerKey };

  console.log(request);
  return (
    <div className="w-screen flex flex-col items-center p-5">
      <p className="w-5/6 m-auto font-bold">
        {JSON.stringify(request, undefined, 4)}
      </p>
    </div>
  );
}
