import { Link, Navigate } from "react-router-dom";
import { useStore } from "../stores/store";
import { Button, Switch } from "@nextui-org/react";

function handlePageSelect(pageNumber, isSelected) {
  let worksheet = useStore.getState().worksheets[0];

  worksheet.pages[pageNumber - 1].isSelected = isSelected;

  useStore.setState({ worksheets: [worksheet] });
}

function PagePreviews(props) {
  let worksheet = props.worksheet;

  let pages = worksheet.pages;

  pages.sort((a, b) => {
    return a.pageNumber - b.pageNumber;
  });

  return (
    <>
      {worksheet.pages.map((page) => {
        return (
          <div className="flex flex-col items-center bg-foreground-100 p-5">
            <img className="border-black border-1" src={page.previewURL}></img>
            <br></br>
            <Switch
              isSelected={page.isSelected}
              onValueChange={(isSelected) => {
                handlePageSelect(page.pageNumber, isSelected);
              }}
            />
          </div>
        );
      })}
    </>
  );
}

export default function SelectPage() {
  const worksheets = useStore((state) => state.worksheets);
  if (worksheets.length == 0) {
    return <Navigate to="/upload" />;
  }

  const worksheet = worksheets[0];

  console.log(worksheet);
  return (
    <div className="w-screen flex flex-col items-center gap-10">
      <p className="text-5xl font-bold">Select Pages</p>

      <div className="grid grid-cols-3 w-2/3 gap-2 h-auto">
        <PagePreviews worksheet={worksheet} />
      </div>

      <Button color="primary" className="my-10">
        <Link to="/answerkey">Next</Link>
      </Button>
    </div>
  );
}
