import { Link } from "react-router-dom";
import {
  useStore,
  useLoadingStateStore,
  Worksheet,
  Page,
} from "../stores/store";
import { Button } from "@nextui-org/react";
import { useState } from "react";

const PDFJS = require("pdfjs-dist/webpack");

function addPagesToWorksheet(worksheet) {
  console.log("Started Pages to Worksheet procedure.");
  useLoadingStateStore.setState({ isLoading: true });
  let pdfBase64 = atob(worksheet.dataBase64);

  PDFJS.getDocument({ data: pdfBase64 }).promise.then((pdf) => {
    const pages = [];
    const promises = [];

    for (let i = 0; i < pdf.numPages; i++) {
      const promise = pdf.getPage(i + 1).then((page) => {
        // the result is the base 64 version of the image
        return new Promise((resolve) => {
          const canvas = document.createElement("canvas");
          const context = canvas.getContext("2d");
          const viewport = page.getViewport({ scale: 1 });
          canvas.width = viewport.width;
          canvas.height = viewport.height;
          const renderContext = {
            canvasContext: context,
            viewport: viewport,
          };

          page.render(renderContext).promise.then(() => {
            // Get the data URL of the rendered page
            const dataURL = canvas.toDataURL("image/png"); // You can choose a different format if needed

            let pageObj = new Page(dataURL, page.pageNumber, true);

            worksheet.addPage(pageObj);

            resolve(dataURL);
          });
        });
      });

      promises.push(promise);
    }

    // Wait for all promises to resolve
    Promise.all(promises).then((dataURLs) => {
      console.log("All pages processed.");

      worksheet.pages.sort((a, b) => {
        return a.pageNumber - b.pageNumber;
      });

      useStore.getState().addWorksheet(worksheet);
    });
  });
}

function handlePdfUpload() {
  const worksheetFile = document.getElementById("worksheet");

  if (worksheetFile.files.length > 0) {
    const file = worksheetFile.files[0];
    const reader = new FileReader();

    reader.onload = function (event) {
      const base64String = event.target.result.split(",")[1];

      let worksheet = new Worksheet(base64String);

      addPagesToWorksheet(worksheet);
    };

    reader.readAsDataURL(file);
  } else {
    alert("Please select a PDF file.");
  }
}

export default function UploadPage() {
  const worksheets = useStore((state) => state.worksheets);

  const [hasFile, setHasFile] = useState(false);
  const [filename, setFilename] = useState("Click here to upload worksheet");

  const onFileChange = (event) => {
    console.log(event)
    if (event.target.files.length > 0) {
      setHasFile(true);
      console.log(event.target.files[0])
      setFilename("Selected: " + event.target.files[0].name)
    }
  };

  return (
    <div className="w-screen h-screen flex flex-col items-center justify-center gap-10">
      <p className="text-5xl font-bold">Upload Worksheet</p>

      <label
        className="text-xl p-10 bg-foreground-200 rounded-xl"
        id="worksheetInputLabel"
      >
        <input
          type="file"
          className="hidden"
          name="worksheet"
          id="worksheet"
          onChange={onFileChange}
        />
        {filename}
      </label>

      <Button onClick={handlePdfUpload} color={worksheets.length > 0 ? "success" : "primary"} isDisabled={!hasFile}>
        {worksheets.length > 0 ? "Uploaded Successfully" : "Upload"}
      </Button>

      { worksheets.length > 0 && 
        <Button color="primary">
          <Link to="/select">
            Next
          </Link>
        </Button>
      }
    </div>
  );
}
