import {
  Modal,
  ModalContent,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Button,
  useDisclosure,
  Input,
} from "@nextui-org/react";
import { useStore } from "../stores/store";
import { useRef, useState } from "react";
import { Link, Navigate } from "react-router-dom";

function initAnswerKey() {
  let nQuestions = document.getElementById("nQuestions").value;
  let answerKey = {};

  for (let i = 1; i <= nQuestions; i++) {
    answerKey[`Q${i}`] = "";
  }

  let worksheet = useStore.getState().worksheets[0];

  worksheet.answerKey = answerKey;

  useStore.setState({ worksheets: [worksheet] });
}

function Answer(props) {
  const qnNo = props.qnNo;

  let worksheet = useStore.getState().worksheets[0];
  let answerKey = worksheet.answerKey;

  const onInputChange = (value) => {
    answerKey[qnNo] = value;
    worksheet.answerKey = answerKey;

    useStore.setState({ worksheets: [worksheet] });

    console.log(worksheet);
  };

  return (
    <div className="flex flex-row items-center p-5 gap-5 bg-foreground-200 rounded-xl">
      <p className="font-bold">{qnNo}</p>
      <Input
        type="text"
        onValueChange={onInputChange}
        value={answerKey[qnNo]}
      />
    </div>
  );
}

export default function AnswerKeyPage() {
  const worksheets = useStore((state) => state.worksheets);
  const { isOpen, onOpen, onOpenChange } = useDisclosure();

  if (worksheets.length == 0) {
    return <Navigate to="/upload" />;
  }

  let answerKey = {};

  if (worksheets[0]) {
    answerKey = worksheets[0].answerKey;
  }

  console.log(answerKey);

  let isComplete = true;

  for (let qnNo of Object.keys(answerKey)) {
    console.log(answerKey[qnNo]);

    if (!answerKey[qnNo]) {
      isComplete = false;
    }
  }

  console.log("Is complete: " + isComplete);

  return (
    <div className="flex flex-col items-center p-5">
      <Modal isOpen={isOpen} onOpenChange={onOpenChange}>
        <ModalContent>
          {(onClose) => (
            <>
              <ModalHeader className="flex flex-col gap-1">
                {Object.keys(answerKey).length === 0
                  ? "Add Questions"
                  : "Change Number of Questions"}
              </ModalHeader>
              <ModalBody>
                <p>Enter number of questions (1-100)</p>

                <Input id="nQuestions"></Input>
              </ModalBody>
              <ModalFooter>
                <Button
                  color="primary"
                  onPress={() => {
                    initAnswerKey();
                    onClose();
                  }}
                >
                  Ok
                </Button>
              </ModalFooter>
            </>
          )}
        </ModalContent>
      </Modal>

      <p className="text-5xl font-bold m-10">Answer Key</p>

      <Button onPress={onOpen} className="m-5">
        {Object.keys(answerKey).length === 0
          ? "Add Questions"
          : "Change Number of Questions"}
      </Button>

      <div className="grid grid-cols-4 gap-5">
        {Object.keys(answerKey).map((qnNo) => {
          return <Answer qnNo={qnNo} />;
        })}
      </div>

      {isComplete && (
        <Button
          className="m-10"
          color="primary"
          onPress={() => console.log(worksheets[0])}
        >
          <Link to="/test">
            Submit
          </Link>
        </Button>
      )}
    </div>
  );
}
